/* subtle glow effect for headers */
@-webkit-keyframes contact_style_glow__36D6R {
    0% {color: rgba(255,255,255,.75); text-shadow: 1px 1px 1px rgba(255,255,255,.75);}
    50% {color: whitesmoke; text-shadow: 2px 1px 1px rgba(245, 245, 245, 0.788);}
    100% {color: rgba(255,255,255,.75); text-shadow: 1px 1px 1px rgba(255,255,255,.75);}
}
@keyframes contact_style_glow__36D6R {
    0% {color: rgba(255,255,255,.75); text-shadow: 1px 1px 1px rgba(255,255,255,.75);}
    50% {color: whitesmoke; text-shadow: 2px 1px 1px rgba(245, 245, 245, 0.788);}
    100% {color: rgba(255,255,255,.75); text-shadow: 1px 1px 1px rgba(255,255,255,.75);}
}

/* removes dark 'mask' from components on hover or reveals components on load */
@-webkit-keyframes contact_style_un-mask__2AvSb {
    from {opacity: 0;}
    to {opacity: 1;}
}
@keyframes contact_style_un-mask__2AvSb {
    from {opacity: 0;}
    to {opacity: 1;}
}

/* loading spinner - outside */
@-webkit-keyframes contact_style_spinner__28-QA {
    0% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(0deg);transform: translate3d(-50%, -50%, 0) rotate(0deg);}
    90% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(365deg);transform: translate3d(-50%, -50%, 0) rotate(365deg);}
    100% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(360deg);transform: translate3d(-50%, -50%, 0) rotate(360deg);}
}
@keyframes contact_style_spinner__28-QA {
    0% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(0deg);transform: translate3d(-50%, -50%, 0) rotate(0deg);}
    90% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(365deg);transform: translate3d(-50%, -50%, 0) rotate(365deg);}
    100% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(360deg);transform: translate3d(-50%, -50%, 0) rotate(360deg);}
}

/* loading spinner - inside */
@-webkit-keyframes contact_style_reverse-spinner__1773J {
    0% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(0deg);transform: translate3d(-50%, -50%, 0) rotate(0deg);}
    100% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(-360deg);transform: translate3d(-50%, -50%, 0) rotate(-360deg);}
}
@keyframes contact_style_reverse-spinner__1773J {
    0% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(0deg);transform: translate3d(-50%, -50%, 0) rotate(0deg);}
    100% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(-360deg);transform: translate3d(-50%, -50%, 0) rotate(-360deg);}
}

/* border flicker */
@-webkit-keyframes contact_style_flash__2Xqaa {
    0%, 60% {border-color: transparent;}
